"use client";

import React, { useState, useEffect } from "react";
import { Button } from "./ui/button";
import { Input } from "./ui/input";
import { Textarea } from "./ui/textarea";
import { Card, CardContent, CardHeader, CardTitle } from "./ui/card";
import aiService from "../services/AIService";

interface CreativeGeneratorProps {
  type: "music" | "art" | "video" | "sfx";
}

interface GeneratedContent {
  id: string;
  type: string;
  prompt: string;
  result: string;
  timestamp: Date;
  status: "generating" | "completed" | "error";
  metadata?: Record<string, any>;
}

const EnhancedCreativeGenerator: React.FC<CreativeGeneratorProps> = ({ type }) => {
  const [prompt, setPrompt] = useState("");
  const [isGenerating, setIsGenerating] = useState(false);
  const [generatedContent, setGeneratedContent] = useState<GeneratedContent[]>([]);
  const [apiKey, setApiKey] = useState("");
  const [showApiKeyInput, setShowApiKeyInput] = useState(false);
  const [isConfigured, setIsConfigured] = useState(false);

  useEffect(() => {
    setIsConfigured(aiService.isReady());
  }, []);

  const typeConfig = {
    music: {
      title: "AI Music Generation",
      description: "Create original music concepts and compositions with AI",
      placeholder: "Describe the music you want to create (e.g., 'upbeat electronic dance music with piano melodies')",
      examples: [
        "Peaceful piano melody for relaxation and meditation",
        "Energetic rock guitar riff with driving drums",
        "Ambient electronic soundscape with ethereal pads",
        "Classical orchestral piece with strings and brass"
      ],
      icon: "ðŸŽµ"
    },
    art: {
      title: "AI Art Generation",
      description: "Generate digital artwork concepts and visual ideas with AI",
      placeholder: "Describe the artwork you want to create (e.g., 'anime-style character with blue hair in a magical forest')",
      examples: [
        "Anime boy with cyan hair and blue eyes in a futuristic setting",
        "Magical forest with glowing trees and floating crystals",
        "Abstract geometric patterns with vibrant colors",
        "Cyberpunk cityscape at sunset with neon lights"
      ],
      icon: "ðŸŽ¨"
    },
    video: {
      title: "AI Video Generation",
      description: "Create animated video concepts and storyboards with AI",
      placeholder: "Describe the video you want to create (e.g., 'short animation of a character exploring a magical world')",
      examples: [
        "Character walking animation with smooth transitions",
        "Particle effects explosion with dynamic lighting",
        "Camera pan across a fantasy landscape",
        "Text animation with kinetic typography effects"
      ],
      icon: "ðŸŽ¬"
    },
    sfx: {
      title: "AI Sound Effects Generation",
      description: "Generate custom sound effect concepts and audio ideas with AI",
      placeholder: "Describe the sound effect you want (e.g., 'magical sparkle sound with ethereal chimes')",
      examples: [
        "Magical sparkle and chime sounds for spell casting",
        "Futuristic beep and notification sounds for UI",
        "Nature sounds like gentle rain and wind through trees",
        "Mechanical and robotic sounds for sci-fi scenes"
      ],
      icon: "ðŸ”Š"
    }
  };

  const config = typeConfig[type];

  const handleApiKeySubmit = () => {
    if (apiKey.trim()) {
      aiService.setApiKey(apiKey.trim());
      setIsConfigured(true);
      setShowApiKeyInput(false);
      setApiKey("");
    }
  };

  const generateContent = async () => {
    if (!prompt.trim()) return;

    if (!isConfigured) {
      setShowApiKeyInput(true);
      return;
    }

    setIsGenerating(true);
    
    const newContent: GeneratedContent = {
      id: `gen_${Date.now()}`,
      type,
      prompt,
      result: "",
      timestamp: new Date(),
      status: "generating"
    };

    setGeneratedContent(prev => [newContent, ...prev]);

    try {
      const response = await aiService.generateCreativeContent(type, prompt);
      
      if (response.success && response.result) {
        setGeneratedContent(prev => 
          prev.map(item => 
            item.id === newContent.id 
              ? { 
                  ...item, 
                  result: response.result!, 
                  status: "completed" as const,
                  metadata: response.metadata
                }
              : item
          )
        );
      } else {
        setGeneratedContent(prev => 
          prev.map(item => 
            item.id === newContent.id 
              ? { 
                  ...item, 
                  result: response.error || "Generation failed", 
                  status: "error" as const
                }
              : item
          )
        );
      }
    } catch (error) {
      setGeneratedContent(prev => 
        prev.map(item => 
          item.id === newContent.id 
            ? { 
                ...item, 
                result: "An error occurred during generation", 
                status: "error" as const
              }
            : item
        )
      );
    } finally {
      setIsGenerating(false);
      setPrompt("");
    }
  };

  const useExample = (example: string) => {
    setPrompt(example);
  };

  const deleteContent = (contentId: string) => {
    setGeneratedContent(prev => prev.filter(item => item.id !== contentId));
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center space-x-3">
        <span className="text-3xl">{config.icon}</span>
        <div>
          <h2 className="text-2xl font-bold text-white">{config.title}</h2>
          <p className="text-gray-300">{config.description}</p>
        </div>
      </div>

      {/* API Configuration */}
      {!isConfigured && (
        <Card className="bg-red-900/20 border-red-500">
          <CardHeader>
            <CardTitle className="text-red-300">AI Service Configuration Required</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <p className="text-gray-300">
              To use real AI-powered generation, please provide an OpenRouter API key.
            </p>
            <Button
              onClick={() => setShowApiKeyInput(true)}
              className="bg-red-600 hover:bg-red-700 text-white"
            >
              Configure AI Service
            </Button>
          </CardContent>
        </Card>
      )}

      {showApiKeyInput && (
        <Card className="bg-gray-800 border-yellow-500">
          <CardHeader>
            <CardTitle className="text-yellow-300">Enter OpenRouter API Key</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <p className="text-sm text-gray-300">
              Get your API key from <a href="https://openrouter.ai" target="_blank" rel="noopener noreferrer" className="text-cyan-400 hover:underline">OpenRouter.ai</a>
            </p>
            <div className="flex space-x-2">
              <Input
                type="password"
                value={apiKey}
                onChange={(e) => setApiKey(e.target.value)}
                placeholder="sk-or-..."
                className="bg-gray-900 text-white border-gray-600"
              />
              <Button onClick={handleApiKeySubmit} className="bg-yellow-600 hover:bg-yellow-700">
                Save
              </Button>
              <Button 
                onClick={() => setShowApiKeyInput(false)} 
                variant="outline"
                className="border-gray-600"
              >
                Cancel
              </Button>
            </div>
          </CardContent>
        </Card>
      )}

      {/* Generation Interface */}
      <Card className="bg-gray-800 border-cyan-500">
        <CardHeader>
          <CardTitle className="text-cyan-300">Create New {type.charAt(0).toUpperCase() + type.slice(1)}</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <Textarea
            value={prompt}
            onChange={(e) => setPrompt(e.target.value)}
            placeholder={config.placeholder}
            className="bg-gray-900 text-white border-gray-600 min-h-24"
          />
          
          <div className="flex justify-between items-center">
            <Button
              onClick={generateContent}
              disabled={!prompt.trim() || isGenerating}
              className="bg-cyan-600 hover:bg-cyan-700 text-white"
            >
              {isGenerating ? (
                <div className="flex items-center space-x-2">
                  <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                  <span>Generating with AI...</span>
                </div>
              ) : (
                `Generate ${type.charAt(0).toUpperCase() + type.slice(1)} with AI`
              )}
            </Button>
            
            <div className="flex items-center space-x-4">
              {isConfigured && (
                <span className="text-xs text-green-400 flex items-center">
                  <div className="w-2 h-2 bg-green-400 rounded-full mr-2"></div>
                  AI Ready
                </span>
              )}
              <span className="text-sm text-gray-400">
                {generatedContent.length} items created
              </span>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Example Prompts */}
      <Card className="bg-gray-800 border-purple-500">
        <CardHeader>
          <CardTitle className="text-purple-300">Example Prompts</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-2">
            {config.examples.map((example, index) => (
              <Button
                key={index}
                variant="outline"
                onClick={() => useExample(example)}
                className="text-left justify-start h-auto p-3 border-gray-600 text-gray-300 hover:text-white hover:border-purple-500"
              >
                {example}
              </Button>
            ))}
          </div>
        </CardContent>
      </Card>

      {/* Generated Content History */}
      {generatedContent.length > 0 && (
        <Card className="bg-gray-800 border-green-500">
          <CardHeader>
            <CardTitle className="text-green-300">AI Generated Content</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4 max-h-96 overflow-y-auto">
              {generatedContent.map((content) => (
                <div key={content.id} className="bg-gray-900 p-4 rounded border border-gray-700">
                  <div className="flex justify-between items-start mb-3">
                    <div className="flex items-center space-x-2">
                      <span className={`px-2 py-1 rounded text-xs ${
                        content.status === "generating" ? "bg-yellow-600 text-white" :
                        content.status === "completed" ? "bg-green-600 text-white" :
                        "bg-red-600 text-white"
                      }`}>
                        {content.status}
                      </span>
                      <span className="text-xs text-gray-500">
                        {content.timestamp.toLocaleTimeString()}
                      </span>
                    </div>
                    <Button
                      onClick={() => deleteContent(content.id)}
                      variant="outline"
                      size="sm"
                      className="text-red-400 border-red-400 hover:bg-red-400 hover:text-white"
                    >
                      Delete
                    </Button>
                  </div>
                  
                  <div className="mb-3">
                    <p className="text-sm text-gray-400 font-medium mb-1">Prompt:</p>
                    <p className="text-sm text-gray-300 bg-gray-800 p-2 rounded">{content.prompt}</p>
                  </div>
                  
                  {content.result && (
                    <div>
                      <p className="text-sm text-gray-400 font-medium mb-1">AI Generated Result:</p>
                      <div className="text-sm text-white bg-black/30 p-3 rounded whitespace-pre-wrap">
                        {content.result}
                      </div>
                    </div>
                  )}
                  
                  {content.metadata && (
                    <div className="mt-2 text-xs text-gray-500">
                      Model: {content.metadata.model} | Tokens: {content.metadata.usage?.total_tokens || 'N/A'}
                    </div>
                  )}
                  
                  {content.status === "generating" && (
                    <div className="flex items-center space-x-2 mt-3">
                      <div className="w-2 h-2 bg-cyan-400 rounded-full animate-bounce"></div>
                      <div className="w-2 h-2 bg-cyan-400 rounded-full animate-bounce" style={{ animationDelay: "0.1s" }}></div>
                      <div className="w-2 h-2 bg-cyan-400 rounded-full animate-bounce" style={{ animationDelay: "0.2s" }}></div>
                      <span className="text-xs text-cyan-400 ml-2">Cyan is creating with AI...</span>
                    </div>
                  )}
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      )}

      {/* Cyan's AI Notes */}
      <Card className="bg-gray-800 border-cyan-500">
        <CardHeader>
          <CardTitle className="text-cyan-300">Cyan's AI Creation Notes</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="text-sm text-gray-300 space-y-2">
            <p>ðŸ¤– <strong>Real AI Power:</strong> I use advanced AI models to generate truly creative and unique content!</p>
            <p>ðŸŽ¯ <strong>Smart Prompts:</strong> The more detailed your description, the better I can create what you envision!</p>
            <p>ðŸ”„ <strong>Iterative Creation:</strong> Try different variations and build upon previous results!</p>
            <p>ðŸ’¡ <strong>Learning Together:</strong> Each generation helps me understand your creative style better!</p>
            <p>ðŸš€ <strong>Endless Possibilities:</strong> From simple concepts to complex creative projects, I'm here to help!</p>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default EnhancedCreativeGenerator;
