"use client";

import React, { useState, useEffect } from "react";
import { Button } from "./ui/button";
import { Input } from "./ui/input";
import { Textarea } from "./ui/textarea";
import { Card, CardContent, CardHeader, CardTitle } from "./ui/card";
import { Checkbox } from "./ui/checkbox";

interface Task {
  id: string;
  title: string;
  description: string;
  completed: boolean;
  priority: "low" | "medium" | "high";
  dueDate?: Date;
  createdAt: Date;
  category: string;
}

interface Reminder {
  id: string;
  title: string;
  message: string;
  time: Date;
  recurring: boolean;
  active: boolean;
}

interface Note {
  id: string;
  title: string;
  content: string;
  createdAt: Date;
  updatedAt: Date;
  tags: string[];
}

const ProductivityRoom: React.FC = () => {
  const [tasks, setTasks] = useState<Task[]>([]);
  const [reminders, setReminders] = useState<Reminder[]>([]);
  const [notes, setNotes] = useState<Note[]>([]);
  const [activeTab, setActiveTab] = useState<"tasks" | "reminders" | "notes">("tasks");

  // Task management
  const [newTask, setNewTask] = useState({
    title: "",
    description: "",
    priority: "medium" as Task["priority"],
    category: "General"
  });

  // Reminder management
  const [newReminder, setNewReminder] = useState({
    title: "",
    message: "",
    time: "",
    recurring: false
  });

  // Note management
  const [newNote, setNewNote] = useState({
    title: "",
    content: "",
    tags: ""
  });

  // Load data from localStorage on mount
  useEffect(() => {
    const savedTasks = localStorage.getItem("cyan-tasks");
    const savedReminders = localStorage.getItem("cyan-reminders");
    const savedNotes = localStorage.getItem("cyan-notes");

    if (savedTasks) setTasks(JSON.parse(savedTasks));
    if (savedReminders) setReminders(JSON.parse(savedReminders));
    if (savedNotes) setNotes(JSON.parse(savedNotes));
  }, []);

  // Save data to localStorage
  useEffect(() => {
    localStorage.setItem("cyan-tasks", JSON.stringify(tasks));
  }, [tasks]);

  useEffect(() => {
    localStorage.setItem("cyan-reminders", JSON.stringify(reminders));
  }, [reminders]);

  useEffect(() => {
    localStorage.setItem("cyan-notes", JSON.stringify(notes));
  }, [notes]);

  // Check for due reminders
  useEffect(() => {
    const checkReminders = () => {
      const now = new Date();
      reminders.forEach(reminder => {
        if (reminder.active && new Date(reminder.time) <= now) {
          // Show notification (in a real app, this would be a proper notification)
          alert(`Reminder: ${reminder.title}\n${reminder.message}`);
          
          if (!reminder.recurring) {
            setReminders(prev => prev.map(r => 
              r.id === reminder.id ? { ...r, active: false } : r
            ));
          }
        }
      });
    };

    const interval = setInterval(checkReminders, 60000); // Check every minute
    return () => clearInterval(interval);
  }, [reminders]);

  // Task functions
  const addTask = () => {
    if (!newTask.title.trim()) return;

    const task: Task = {
      id: `task_${Date.now()}`,
      title: newTask.title,
      description: newTask.description,
      completed: false,
      priority: newTask.priority,
      createdAt: new Date(),
      category: newTask.category
    };

    setTasks(prev => [task, ...prev]);
    setNewTask({ title: "", description: "", priority: "medium", category: "General" });
  };

  const toggleTask = (taskId: string) => {
    setTasks(prev => prev.map(task => 
      task.id === taskId ? { ...task, completed: !task.completed } : task
    ));
  };

  const deleteTask = (taskId: string) => {
    setTasks(prev => prev.filter(task => task.id !== taskId));
  };

  // Reminder functions
  const addReminder = () => {
    if (!newReminder.title.trim() || !newReminder.time) return;

    const reminder: Reminder = {
      id: `reminder_${Date.now()}`,
      title: newReminder.title,
      message: newReminder.message,
      time: new Date(newReminder.time),
      recurring: newReminder.recurring,
      active: true
    };

    setReminders(prev => [reminder, ...prev]);
    setNewReminder({ title: "", message: "", time: "", recurring: false });
  };

  const deleteReminder = (reminderId: string) => {
    setReminders(prev => prev.filter(reminder => reminder.id !== reminderId));
  };

  // Note functions
  const addNote = () => {
    if (!newNote.title.trim()) return;

    const note: Note = {
      id: `note_${Date.now()}`,
      title: newNote.title,
      content: newNote.content,
      createdAt: new Date(),
      updatedAt: new Date(),
      tags: newNote.tags.split(",").map(tag => tag.trim()).filter(tag => tag)
    };

    setNotes(prev => [note, ...prev]);
    setNewNote({ title: "", content: "", tags: "" });
  };

  const deleteNote = (noteId: string) => {
    setNotes(prev => prev.filter(note => note.id !== noteId));
  };

  const getPriorityColor = (priority: Task["priority"]) => {
    switch (priority) {
      case "high": return "border-red-500 bg-red-900/20";
      case "medium": return "border-yellow-500 bg-yellow-900/20";
      case "low": return "border-green-500 bg-green-900/20";
    }
  };

  const getTaskStats = () => {
    const total = tasks.length;
    const completed = tasks.filter(t => t.completed).length;
    const pending = total - completed;
    const highPriority = tasks.filter(t => t.priority === "high" && !t.completed).length;
    
    return { total, completed, pending, highPriority };
  };

  const stats = getTaskStats();

  return (
    <div className="space-y-6">
      <div>
        <h2 className="text-2xl font-bold text-white mb-2">Productivity Room</h2>
        <p className="text-gray-300">Task management, reminders, and note-taking</p>
      </div>

      {/* Stats Overview */}
      <div className="grid grid-cols-4 gap-4">
        <Card className="bg-gray-800 border-blue-500">
          <CardContent className="p-4 text-center">
            <div className="text-2xl font-bold text-blue-400">{stats.total}</div>
            <div className="text-sm text-gray-300">Total Tasks</div>
          </CardContent>
        </Card>
        <Card className="bg-gray-800 border-green-500">
          <CardContent className="p-4 text-center">
            <div className="text-2xl font-bold text-green-400">{stats.completed}</div>
            <div className="text-sm text-gray-300">Completed</div>
          </CardContent>
        </Card>
        <Card className="bg-gray-800 border-yellow-500">
          <CardContent className="p-4 text-center">
            <div className="text-2xl font-bold text-yellow-400">{stats.pending}</div>
            <div className="text-sm text-gray-300">Pending</div>
          </CardContent>
        </Card>
        <Card className="bg-gray-800 border-red-500">
          <CardContent className="p-4 text-center">
            <div className="text-2xl font-bold text-red-400">{stats.highPriority}</div>
            <div className="text-sm text-gray-300">High Priority</div>
          </CardContent>
        </Card>
      </div>

      {/* Tab Navigation */}
      <div className="flex space-x-4 border-b border-gray-700">
        {["tasks", "reminders", "notes"].map((tab) => (
          <button
            key={tab}
            onClick={() => setActiveTab(tab as typeof activeTab)}
            className={`pb-2 px-4 capitalize ${
              activeTab === tab 
                ? "border-b-2 border-cyan-500 text-cyan-400" 
                : "text-gray-400 hover:text-white"
            }`}
          >
            {tab}
          </button>
        ))}
      </div>

      {/* Tasks Tab */}
      {activeTab === "tasks" && (
        <div className="space-y-4">
          <Card className="bg-gray-800 border-cyan-500">
            <CardHeader>
              <CardTitle className="text-cyan-300">Add New Task</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <Input
                value={newTask.title}
                onChange={(e) => setNewTask(prev => ({ ...prev, title: e.target.value }))}
                placeholder="Task title..."
                className="bg-gray-900 text-white border-gray-600"
              />
              <Textarea
                value={newTask.description}
                onChange={(e) => setNewTask(prev => ({ ...prev, description: e.target.value }))}
                placeholder="Task description..."
                className="bg-gray-900 text-white border-gray-600"
              />
              <div className="flex space-x-4">
                <select
                  value={newTask.priority}
                  onChange={(e) => setNewTask(prev => ({ ...prev, priority: e.target.value as Task["priority"] }))}
                  className="bg-gray-900 text-white border border-gray-600 rounded p-2"
                >
                  <option value="low">Low Priority</option>
                  <option value="medium">Medium Priority</option>
                  <option value="high">High Priority</option>
                </select>
                <Input
                  value={newTask.category}
                  onChange={(e) => setNewTask(prev => ({ ...prev, category: e.target.value }))}
                  placeholder="Category"
                  className="bg-gray-900 text-white border-gray-600"
                />
                <Button onClick={addTask} className="bg-cyan-600 hover:bg-cyan-700">
                  Add Task
                </Button>
              </div>
            </CardContent>
          </Card>

          <div className="space-y-3">
            {tasks.map((task) => (
              <Card key={task.id} className={`bg-gray-800 ${getPriorityColor(task.priority)}`}>
                <CardContent className="p-4">
                  <div className="flex items-start justify-between">
                    <div className="flex items-start space-x-3 flex-1">
                      <Checkbox
                        checked={task.completed}
                        onCheckedChange={() => toggleTask(task.id)}
                        className="mt-1"
                      />
                      <div className="flex-1">
                        <h3 className={`font-semibold ${task.completed ? 'line-through text-gray-500' : 'text-white'}`}>
                          {task.title}
                        </h3>
                        {task.description && (
                          <p className={`text-sm mt-1 ${task.completed ? 'text-gray-600' : 'text-gray-300'}`}>
                            {task.description}
                          </p>
                        )}
                        <div className="flex items-center space-x-4 mt-2 text-xs text-gray-400">
                          <span className={`px-2 py-1 rounded ${
                            task.priority === 'high' ? 'bg-red-600' :
                            task.priority === 'medium' ? 'bg-yellow-600' :
                            'bg-green-600'
                          } text-white`}>
                            {task.priority}
                          </span>
                          <span>{task.category}</span>
                          <span>{task.createdAt.toLocaleDateString()}</span>
                        </div>
                      </div>
                    </div>
                    <Button
                      onClick={() => deleteTask(task.id)}
                      variant="outline"
                      size="sm"
                      className="text-red-400 border-red-400 hover:bg-red-400 hover:text-white"
                    >
                      Delete
                    </Button>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      )}

      {/* Reminders Tab */}
      {activeTab === "reminders" && (
        <div className="space-y-4">
          <Card className="bg-gray-800 border-cyan-500">
            <CardHeader>
              <CardTitle className="text-cyan-300">Add New Reminder</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <Input
                value={newReminder.title}
                onChange={(e) => setNewReminder(prev => ({ ...prev, title: e.target.value }))}
                placeholder="Reminder title..."
                className="bg-gray-900 text-white border-gray-600"
              />
              <Textarea
                value={newReminder.message}
                onChange={(e) => setNewReminder(prev => ({ ...prev, message: e.target.value }))}
                placeholder="Reminder message..."
                className="bg-gray-900 text-white border-gray-600"
              />
              <div className="flex space-x-4 items-center">
                <input
                  type="datetime-local"
                  value={newReminder.time}
                  onChange={(e) => setNewReminder(prev => ({ ...prev, time: e.target.value }))}
                  className="bg-gray-900 text-white border border-gray-600 rounded p-2"
                />
                <label className="flex items-center space-x-2 text-white">
                  <Checkbox
                    checked={newReminder.recurring}
                    onCheckedChange={(checked) => setNewReminder(prev => ({ ...prev, recurring: !!checked }))}
                  />
                  <span>Recurring</span>
                </label>
                <Button onClick={addReminder} className="bg-cyan-600 hover:bg-cyan-700">
                  Add Reminder
                </Button>
              </div>
            </CardContent>
          </Card>

          <div className="space-y-3">
            {reminders.map((reminder) => (
              <Card key={reminder.id} className="bg-gray-800 border-purple-500">
                <CardContent className="p-4">
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      <h3 className="font-semibold text-white">{reminder.title}</h3>
                      <p className="text-sm text-gray-300 mt-1">{reminder.message}</p>
                      <div className="flex items-center space-x-4 mt-2 text-xs text-gray-400">
                        <span>{new Date(reminder.time).toLocaleString()}</span>
                        {reminder.recurring && <span className="bg-purple-600 text-white px-2 py-1 rounded">Recurring</span>}
                        <span className={`px-2 py-1 rounded ${reminder.active ? 'bg-green-600' : 'bg-gray-600'} text-white`}>
                          {reminder.active ? 'Active' : 'Inactive'}
                        </span>
                      </div>
                    </div>
                    <Button
                      onClick={() => deleteReminder(reminder.id)}
                      variant="outline"
                      size="sm"
                      className="text-red-400 border-red-400 hover:bg-red-400 hover:text-white"
                    >
                      Delete
                    </Button>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      )}

      {/* Notes Tab */}
      {activeTab === "notes" && (
        <div className="space-y-4">
          <Card className="bg-gray-800 border-cyan-500">
            <CardHeader>
              <CardTitle className="text-cyan-300">Add New Note</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <Input
                value={newNote.title}
                onChange={(e) => setNewNote(prev => ({ ...prev, title: e.target.value }))}
                placeholder="Note title..."
                className="bg-gray-900 text-white border-gray-600"
              />
              <Textarea
                value={newNote.content}
                onChange={(e) => setNewNote(prev => ({ ...prev, content: e.target.value }))}
                placeholder="Note content..."
                className="bg-gray-900 text-white border-gray-600 min-h-32"
              />
              <div className="flex space-x-4">
                <Input
                  value={newNote.tags}
                  onChange={(e) => setNewNote(prev => ({ ...prev, tags: e.target.value }))}
                  placeholder="Tags (comma separated)"
                  className="bg-gray-900 text-white border-gray-600"
                />
                <Button onClick={addNote} className="bg-cyan-600 hover:bg-cyan-700">
                  Add Note
                </Button>
              </div>
            </CardContent>
          </Card>

          <div className="space-y-3">
            {notes.map((note) => (
              <Card key={note.id} className="bg-gray-800 border-green-500">
                <CardContent className="p-4">
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      <h3 className="font-semibold text-white">{note.title}</h3>
                      <p className="text-sm text-gray-300 mt-2 whitespace-pre-wrap">{note.content}</p>
                      <div className="flex items-center space-x-2 mt-3">
                        {note.tags.map((tag, index) => (
                          <span key={index} className="bg-green-600 text-white px-2 py-1 rounded text-xs">
                            {tag}
                          </span>
                        ))}
                      </div>
                      <div className="text-xs text-gray-400 mt-2">
                        Created: {note.createdAt.toLocaleString()}
                      </div>
                    </div>
                    <Button
                      onClick={() => deleteNote(note.id)}
                      variant="outline"
                      size="sm"
                      className="text-red-400 border-red-400 hover:bg-red-400 hover:text-white"
                    >
                      Delete
                    </Button>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      )}

      {/* Cyan's Productivity Tips */}
      <Card className="bg-gray-800 border-cyan-500">
        <CardHeader>
          <CardTitle className="text-cyan-300">Cyan's Productivity Tips</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="text-sm text-gray-300 space-y-2">
            <p>ðŸ“ <strong>Task Management:</strong> Break big tasks into smaller, manageable pieces!</p>
            <p>â° <strong>Reminders:</strong> Set reminders for important deadlines and appointments!</p>
            <p>ðŸ“š <strong>Notes:</strong> Keep track of ideas and important information with organized notes!</p>
            <p>ðŸŽ¯ <strong>Priorities:</strong> Focus on high-priority tasks first to stay productive!</p>
            <p>ðŸ”„ <strong>Review:</strong> Regularly review and update your tasks and goals!</p>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default ProductivityRoom;
