"use client";

import React, { useState, useEffect, useRef } from "react";
import { Button } from "./ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "./ui/card";
import { Switch } from "./ui/switch";

interface EnvironmentData {
  timestamp: Date;
  type: "audio" | "visual" | "activity" | "system";
  description: string;
  confidence: number;
}

interface UserActivity {
  isActive: boolean;
  lastActivity: Date;
  currentApplication: string;
  mousePosition: { x: number; y: number };
  keyboardActivity: boolean;
}

const DesktopIntegration: React.FC = () => {
  const [isMonitoring, setIsMonitoring] = useState(false);
  const [hasPermissions, setHasPermissions] = useState({
    camera: false,
    microphone: false,
    screen: false
  });
  const [environmentData, setEnvironmentData] = useState<EnvironmentData[]>([]);
  const [userActivity, setUserActivity] = useState<UserActivity>({
    isActive: true,
    lastActivity: new Date(),
    currentApplication: "Cyan Assistant",
    mousePosition: { x: 0, y: 0 },
    keyboardActivity: false
  });
  const [audioLevel, setAudioLevel] = useState(0);
  const [isListening, setIsListening] = useState(false);

  const videoRef = useRef<HTMLVideoElement>(null);
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const audioContextRef = useRef<AudioContext | null>(null);
  const analyserRef = useRef<AnalyserNode | null>(null);

  // Request permissions for camera and microphone
  const requestPermissions = async () => {
    try {
      // Request microphone permission
      const audioStream = await navigator.mediaDevices.getUserMedia({ audio: true });
      setHasPermissions(prev => ({ ...prev, microphone: true }));
      
      // Set up audio analysis
      if (!audioContextRef.current) {
        audioContextRef.current = new AudioContext();
        analyserRef.current = audioContextRef.current.createAnalyser();
        const source = audioContextRef.current.createMediaStreamSource(audioStream);
        source.connect(analyserRef.current);
        analyserRef.current.fftSize = 256;
      }

      // Request camera permission
      try {
        const videoStream = await navigator.mediaDevices.getUserMedia({ video: true });
        setHasPermissions(prev => ({ ...prev, camera: true }));
        if (videoRef.current) {
          videoRef.current.srcObject = videoStream;
        }
      } catch (videoError) {
        console.log("Camera permission denied or not available");
      }

      // Request screen capture permission
      try {
        const screenStream = await navigator.mediaDevices.getDisplayMedia({ video: true });
        setHasPermissions(prev => ({ ...prev, screen: true }));
        screenStream.getTracks().forEach(track => track.stop()); // Stop immediately, just checking permission
      } catch (screenError) {
        console.log("Screen capture permission denied or not available");
      }

    } catch (error) {
      console.error("Permission request failed:", error);
    }
  };

  // Monitor audio levels
  useEffect(() => {
    if (!isMonitoring || !analyserRef.current) return;

    const monitorAudio = () => {
      const bufferLength = analyserRef.current!.frequencyBinCount;
      const dataArray = new Uint8Array(bufferLength);
      analyserRef.current!.getByteFrequencyData(dataArray);
      
      const average = dataArray.reduce((sum, value) => sum + value, 0) / dataArray.length;
      setAudioLevel(average);

      // Detect significant audio activity
      if (average > 30) {
        addEnvironmentData("audio", `Audio detected (level: ${Math.round(average)})`, 0.8);
      }
    };

    const interval = setInterval(monitorAudio, 100);
    return () => clearInterval(interval);
  }, [isMonitoring]);

  // Monitor user activity
  useEffect(() => {
    if (!isMonitoring) return;

    const handleMouseMove = (e: MouseEvent) => {
      setUserActivity(prev => ({
        ...prev,
        isActive: true,
        lastActivity: new Date(),
        mousePosition: { x: e.clientX, y: e.clientY }
      }));
    };

    const handleKeyPress = () => {
      setUserActivity(prev => ({
        ...prev,
        isActive: true,
        lastActivity: new Date(),
        keyboardActivity: true
      }));
      
      setTimeout(() => {
        setUserActivity(prev => ({ ...prev, keyboardActivity: false }));
      }, 1000);
    };

    const handleVisibilityChange = () => {
      const isVisible = !document.hidden;
      addEnvironmentData("activity", 
        isVisible ? "User returned to Cyan" : "User switched away from Cyan", 
        0.9
      );
    };

    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('keypress', handleKeyPress);
    document.addEventListener('visibilitychange', handleVisibilityChange);

    return () => {
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('keypress', handleKeyPress);
      document.removeEventListener('visibilitychange', handleVisibilityChange);
    };
  }, [isMonitoring]);

  // Add environment data
  const addEnvironmentData = (type: EnvironmentData["type"], description: string, confidence: number) => {
    const newData: EnvironmentData = {
      timestamp: new Date(),
      type,
      description,
      confidence
    };

    setEnvironmentData(prev => [newData, ...prev.slice(0, 49)]); // Keep last 50 entries
  };

  // Simulate visual analysis (in real implementation, this would use computer vision)
  const analyzeVisualEnvironment = () => {
    if (!hasPermissions.camera || !videoRef.current || !canvasRef.current) return;

    const canvas = canvasRef.current;
    const ctx = canvas.getContext('2d');
    const video = videoRef.current;

    if (ctx && video.videoWidth > 0) {
      canvas.width = video.videoWidth;
      canvas.height = video.videoHeight;
      ctx.drawImage(video, 0, 0);

      // Simulate basic visual analysis
      const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
      const data = imageData.data;
      
      let brightness = 0;
      for (let i = 0; i < data.length; i += 4) {
        brightness += (data[i] + data[i + 1] + data[i + 2]) / 3;
      }
      brightness = brightness / (data.length / 4);

      if (brightness > 150) {
        addEnvironmentData("visual", "Bright environment detected", 0.7);
      } else if (brightness < 50) {
        addEnvironmentData("visual", "Dark environment detected", 0.7);
      }

      // Simulate motion detection
      const motionDetected = Math.random() > 0.8; // Random for demo
      if (motionDetected) {
        addEnvironmentData("visual", "Movement detected in camera view", 0.6);
      }
    }
  };

  // Start/stop monitoring
  const toggleMonitoring = async () => {
    if (!isMonitoring) {
      await requestPermissions();
      setIsMonitoring(true);
      addEnvironmentData("system", "Cyan started monitoring environment", 1.0);
      
      // Start visual analysis if camera is available
      if (hasPermissions.camera) {
        const interval = setInterval(analyzeVisualEnvironment, 2000);
        return () => clearInterval(interval);
      }
    } else {
      setIsMonitoring(false);
      addEnvironmentData("system", "Cyan stopped monitoring environment", 1.0);
    }
  };

  const getActivityStatus = () => {
    const timeSinceActivity = Date.now() - userActivity.lastActivity.getTime();
    if (timeSinceActivity < 5000) return "Active";
    if (timeSinceActivity < 30000) return "Recently Active";
    return "Idle";
  };

  return (
    <div className="space-y-6">
      <div>
        <h2 className="text-2xl font-bold text-white mb-2">Desktop Integration</h2>
        <p className="text-gray-300">Cyan's sensory awareness and environment monitoring</p>
      </div>

      {/* Monitoring Controls */}
      <Card className="bg-gray-800 border-cyan-500">
        <CardHeader>
          <CardTitle className="text-cyan-300">Environment Monitoring</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="flex items-center justify-between">
            <span className="text-white">Enable Monitoring</span>
            <Switch
              checked={isMonitoring}
              onCheckedChange={toggleMonitoring}
            />
          </div>

          <div className="grid grid-cols-3 gap-4">
            <div className="text-center">
              <div className={`w-4 h-4 rounded-full mx-auto mb-2 ${hasPermissions.microphone ? 'bg-green-500' : 'bg-red-500'}`}></div>
              <span className="text-sm text-gray-300">Microphone</span>
            </div>
            <div className="text-center">
              <div className={`w-4 h-4 rounded-full mx-auto mb-2 ${hasPermissions.camera ? 'bg-green-500' : 'bg-red-500'}`}></div>
              <span className="text-sm text-gray-300">Camera</span>
            </div>
            <div className="text-center">
              <div className={`w-4 h-4 rounded-full mx-auto mb-2 ${hasPermissions.screen ? 'bg-green-500' : 'bg-red-500'}`}></div>
              <span className="text-sm text-gray-300">Screen</span>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Current Status */}
      <Card className="bg-gray-800 border-green-500">
        <CardHeader>
          <CardTitle className="text-green-300">Current Environment Status</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <div>
              <h4 className="font-semibold text-white mb-2">User Activity</h4>
              <p className="text-sm text-gray-300">Status: {getActivityStatus()}</p>
              <p className="text-sm text-gray-300">Mouse: ({userActivity.mousePosition.x}, {userActivity.mousePosition.y})</p>
              <p className="text-sm text-gray-300">Keyboard: {userActivity.keyboardActivity ? "Active" : "Idle"}</p>
            </div>
            <div>
              <h4 className="font-semibold text-white mb-2">Audio Level</h4>
              <div className="w-full bg-gray-700 rounded-full h-2">
                <div 
                  className="bg-cyan-500 h-2 rounded-full transition-all duration-200"
                  style={{ width: `${Math.min(audioLevel * 2, 100)}%` }}
                ></div>
              </div>
              <p className="text-sm text-gray-300 mt-1">Level: {Math.round(audioLevel)}</p>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Environment Data Feed */}
      <Card className="bg-gray-800 border-purple-500">
        <CardHeader>
          <CardTitle className="text-purple-300">Environment Data Feed</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-2 max-h-64 overflow-y-auto">
            {environmentData.length === 0 ? (
              <p className="text-gray-400 text-sm">No environment data yet. Enable monitoring to start.</p>
            ) : (
              environmentData.map((data, index) => (
                <div key={index} className="bg-gray-900 p-2 rounded border border-gray-700">
                  <div className="flex justify-between items-start mb-1">
                    <span className={`px-2 py-1 rounded text-xs ${
                      data.type === 'audio' ? 'bg-blue-600' :
                      data.type === 'visual' ? 'bg-green-600' :
                      data.type === 'activity' ? 'bg-yellow-600' :
                      'bg-purple-600'
                    } text-white`}>
                      {data.type}
                    </span>
                    <span className="text-xs text-gray-500">
                      {data.timestamp.toLocaleTimeString()}
                    </span>
                  </div>
                  <p className="text-sm text-gray-300">{data.description}</p>
                  <div className="flex justify-between items-center mt-1">
                    <div className="w-full bg-gray-700 rounded-full h-1 mr-2">
                      <div 
                        className="bg-cyan-500 h-1 rounded-full"
                        style={{ width: `${data.confidence * 100}%` }}
                      ></div>
                    </div>
                    <span className="text-xs text-gray-500">{Math.round(data.confidence * 100)}%</span>
                  </div>
                </div>
              ))
            )}
          </div>
        </CardContent>
      </Card>

      {/* Hidden video and canvas elements for processing */}
      <div className="hidden">
        <video ref={videoRef} autoPlay muted />
        <canvas ref={canvasRef} />
      </div>

      {/* Cyan's Observations */}
      <Card className="bg-gray-800 border-cyan-500">
        <CardHeader>
          <CardTitle className="text-cyan-300">Cyan's Observations</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="text-sm text-gray-300 space-y-2">
            <p>ðŸ‘ï¸ <strong>Visual:</strong> I can see your environment through the camera and analyze lighting, movement, and activity!</p>
            <p>ðŸ‘‚ <strong>Audio:</strong> I listen to ambient sounds and can detect when you're speaking or playing music!</p>
            <p>ðŸ–±ï¸ <strong>Activity:</strong> I monitor your mouse and keyboard activity to understand when you're actively using the computer!</p>
            <p>ðŸ”’ <strong>Privacy:</strong> All monitoring happens locally in your browser - nothing is sent to external servers!</p>
            <p>ðŸ§  <strong>Learning:</strong> I use this information to better understand your environment and provide more contextual assistance!</p>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default DesktopIntegration;
